.movie {
  width: 12%;
  margin-bottom: 40px;
}
.movie img {
  width: 100%;
}

.movie_data {
  margin-top: 20px;
}

.movie_title {
  width: 100%;
  height: 48px;
  line-height: 1.2;
}
.movie_title .title_year {
  font-size: 15px;
}

.movie_rating {
  margin-bottom: 12px;
  font-size: 14px;
}

.genres {
  height: 48px;
}
.genres .genres_genre {
  display: inline-block;
  margin-right: 4px;
  font-size: 13px;
}
